export default {
	data() {
		return {
			simple: {
				id: '',
				name: '',
				project: '',
				price: 0
			}
		}
	},
	onLoad() {
		this.help()
	},
	onShow(result) {
		uni.getStorage({
			key: 'data',
			success: (result) => {
				if (result) {
					this.simple = result.data.sample
				}
			}
		});

	},
	onReady() {
		this.help()
	},
	methods: {
		select() {
			uni.navigateTo({
				url: 'selectSample'
			})
		},
		viewdetails() {
			console.log("22")
			uni.navigateTo({
				url: 'sampleNorms'
			})
		},
		back() {
			uni.navigateBack({
				url: 'sampleRegistration'
			})
		},
		submit() {
			console.log(this.simple)
			if (this.simple.id === '') {
				uni.showToast({
					title: '请至少选择一种检测项目',
					mask: true,
					icon: 'none',
					duration: 2000
				});
			}
			uni.setStorage({
				key: 'data',
				data: {
					unit: '',
					addr: '',
					sample: this.simple
				}
			})
			uni.navigateBack({
				url: 'sampleRegistration'

			})
		},
		help() {
			this.simple= {id:'',name: '',project: '',price: 0}
		}

	}
}
